import React, { Component } from "react";
import {
	Tabs,
	Tab,
	Grid,
	Card,
	CardTitle,
	CardActions,
	CardText,
	Button
} from "react-mdl";
import ReadMore from "./readmore";

class Projects extends Component {
	constructor(props) {
		super(props);
		this.state = { activeTab: 0 };
	}
	toggleCategories = () => {
		switch (this.state.activeTab) {
			case 0: // Case 0 == React
				return (
					<div className="projects-grid">
						{/* React project 1: portfolio website */}
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(data:image/png;base64,) center / cover"
								}}
							>
								Portfolio Website
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={`A website made with ReactJS to show my repositories in a more organised manner.`}
								/>
							</CardText>
							<CardActions border>
								<Button
									colored
									href={
										"https://github.com/Taipan95/portfolio-website"
									}
								>
									GitHub
								</Button>
							</CardActions>
						</Card>

						{/* React project 2: OMDB Search*/}
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(data:image/png;base64,) center / cover"
								}}
							>
								OMDB Search
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"A project using OMDB to search for movies by title. "
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									colored
									href={
										"https://github.com/Taipan95/react-omdb"
									}
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 1: // Case 1 == JavaScript
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://a.thumbs.redditmedia.com/zDOFJTXd6fmlD58VDGypiV94Leflz11woxmgbGY6p_4.png) center / cover"
								}}
							>
								OMDB Website
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"A simple website made with JavaScript, using OMDB API to search for movies, implemented in 3 different ways for practice, Bootstrap, CSS Grid and JQueryUI."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									colored
									href={
										"https://github.com/Taipan95/Movie-Search-website"
									}
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 2: // Case 2 == Java
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://regmedia.co.uk/2013/01/30/java_logo.jpg)"
								}}
							>
								Distributed Systems: Matrix factorization
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"A Java Master-Workers project made for university under the course of Distributed Systems."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									href="https://github.com/Taipan95/Distributed-Systems-Java-Project"
									colored
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://regmedia.co.uk/2013/01/30/java_logo.jpg)"
								}}
							>
								Distributed Systems: Android Application
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"An Android Application made using Android Studio for the course of Distributed Systems. It uses the data calculated by the previous Master-Workers system to mark the k-top recommended places for a given user."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									href="https://github.com/Taipan95/Distributed-Systems-Java-Project"
									colored
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://regmedia.co.uk/2013/01/30/java_logo.jpg)"
								}}
							>
								Elastic Search
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"A project developed under the course Information Retrieval Systems, documents are uploaded to an elastic search index using tf-idf weights, queries then get processed to extract the k-top matching documents."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									href="https://github.com/Taipan95/Elastic-Search-project-"
									colored
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 3: // Case 3 == Unity
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://icon-library.net/images/unity-icon-png/unity-icon-png-3.jpg) center / cover"
								}}
							>
								Loading Simulator
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"A fun little project I worked on last summer, it's my approach on an already existing game on Steam."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									colored
									href="https://github.com/Taipan95/Loading-Simulator"
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "250px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px"
									// background:
									// 	"url(https://icon-library.net/images/unity-icon-png/unity-icon-png-3.jpg) center / cover"
								}}
							>
								Fox Mario
							</CardTitle>
							<CardText style={{ minHeight: "80px" }}>
								<ReadMore
									text={
										"Collaboration project between me and some friends, an approach on a Super Mario parody game known as Cat Mario. Sneaky traps everywhere, and pretty much no way to make it to the end. A fun project for educational purposes."
									}
								/>
							</CardText>
							<CardActions border>
								<Button
									colored
									href="https://github.com/Taipan95/Fox-mario"
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
					</div>
				);
			default:
				break;
		}
	};

	render() {
		return (
			<div className="categories">
				<Tabs
					className="categories-tabs"
					activeTab={this.state.activeTab}
					onChange={tabId => this.setState({ activeTab: tabId })}
					ripple
				>
					<Tab
						style={{
							color: "white",
							fontSize: "1em",
							fontWeight: "normal"
						}}
					>
						React
					</Tab>
					<Tab
						style={{
							color: "white",
							fontSize: "1em",
							fontWeight: "normal"
						}}
					>
						JavaScript
					</Tab>
					<Tab
						style={{
							color: "white",
							fontSize: "1em",
							fontWeight: "normal"
						}}
					>
						Java
					</Tab>
					<Tab
						style={{
							color: "white",
							fontSize: "1em",
							fontWeight: "normal"
						}}
					>
						Unity
					</Tab>
				</Tabs>

				<Grid
					col={12}
					style={{
						display: "block",
						width: "100%",
						textAlign: "center"
					}}
				>
					{this.toggleCategories()}
				</Grid>
			</div>
		);
	}
}

export default Projects;
